.container2{
    max-width: 1250px;
    margin-inline: auto;
    margin-bottom: 90px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
    gap: 75px;
    text-align: center;
}
#img{
    width: 30vw;
    margin-inline: auto;
    margin-bottom: 0%;
}
#button a{
    color: black;
    font-size: 1.3rem;
    border: 1px solid black;
    padding: 12px 24px;
    transition: 0.4s ease;
    text-decoration: none;
    
}
#button a:hover{
   background-color: rgb(178, 177, 177);
}
.cardz{
    width: 420px;
    border: 1px ridge;
    justify-content: center;
    
}
.content1{
    width: 350px;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-top: 0%;
    margin-inline: auto;
    
}
    
@media screen and (max-width: 600px) {
    #img{
        width: 65vw;
    }

    .cardz{
    width: 66vw;
    border: 1px ridge;
    justify-content: center;
    font-size: 1.4rem;
}
#button a{
    margin-top: 10px;
}
p{
    margin-bottom: 5px;
}
.content1{
    width: 52vw;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-top: 0%;
    margin-right: 0px;
    margin-inline: auto;
    
    
}
 h1{
    font-size: 1.5rem;
 }
}