/* Add this CSS to your stylesheet */
ol {
  list-style-type: none; /* Remove the default numbering */
  counter-reset: item; /* Reset the counter for list items */
 
}
ol li{
  border: 1px solid black;
  border-radius: 20px;
  padding: 20px;
  margin-bottom: 30px;
  margin-top: 10px;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.2);
  margin-inline: auto;
}

ol li::before {
 
  content: "Rank " counter(item); /* Display "rank" followed by the counter value */
  counter-increment: item; /* Increment the counter for each item */
  font-size: 40px; /* Increase the font size */
  font-weight: bold; /* Make it bold */
  margin-right: 10px; /* Add spacing between the index and the list item */
}
