body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-link{
  font-size: 1.4rem;
}
.logo img{
 width: 100px;
 height: auto;
 z-index: 99;
 
 position: relative;
}

.navbar-nav li{
  padding-inline:20px ;
}
.navbar-nav li a{
 
}
hr{
  box-shadow: 2px black;
}

.header-image img{
  width: 65%;
  border-radius: 20px;
  float: right;
}
.header-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.8rem;
}
.header-text{
  text-align: left;
 
  margin-block: 20px;

}
.header-text h2{
 margin-bottom: 25px;

}
.btn-primary{
  padding: 10px 18px;
  font-size: 1.3rem;
}
#container{
  max-width: 1100px;
  margin-inline: auto;
  
}

