.container{
    max-width: 75vw;
    margin-inline:auto ;
    justify-content: center;
    align-items: center;
}
.form{
    justify-content: center;
    align-items: center;
    margin-block: 20px;
    margin-inline: auto;

}
.inputs{
    margin-block: 20px;
    width: 45vw;
    height: 50px;
    font-size: 1.4rem;
    border-radius: 8px;
    margin-inline: auto;
}
.button{
    margin-inline: auto;
    padding: 10px 25px;
    border-radius: 8px;
    font-size: 1.4rem;
    text-align: center;
    background-color:gray;
}
.button:hover{
    background-color: rgb(98, 98, 98);
}
.login{
    margin-inline: auto;
    
    margin-bottom: 25vh;
    
}

.s-h1{
    font-size: 1.5rem;
}

ul{
    margin-left: 0%;
}
.error{
    color: red;
}
h1{
    margin-bottom: 25px;
}
@media screen and(max-width:700px){
    .inputs{
       width: 70vw;
 
    }
    ul li{
       font-size: 1.2rem;
    }
  
 }