.footer{
    display: flex;
    justify-content: space-between;
}
a{
    margin: 10px;
    margin-top: 32px;
    
}
a i{
    width: 32px;
}
#container{
    max-width: 1250px;
    margin-inline: auto;
    
}
.copy{
    margin-top: 20px;
    text-align: center;
}
@media screen and (max-width: 600px) {
    .footer{
        display: block;
        justify-content: space-between;
        text-align: center;
        align-items: center;
    }

}